import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {ApiProvider} from "../../providers/api/api";
import {BeritaPage} from "../berita/berita";

/**
 * Generated class for the LandingDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-landing-detail',
  templateUrl: 'landing-detail.html',
})
export class LandingDetailPage {
  categories = []
  constructor(public navCtrl: NavController, public navParams: NavParams,private api:ApiProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LandingDetailPage');
  }
  ionViewWillEnter(){
    this.categories =[];
    let colors = [
      "danger",
      "primary"
    ];
    this.api.feed_category_detail(10,1,this.navParams.data.id).subscribe(data=>{
      let x = 0;
      data.data.forEach(item => {
        this.categories.push({
          id:item.id,
          name:item.name,
          color:((x%2 == 0)?colors[0]:colors[1])
        });
        x++;
      })

    },error => {
      this.api._alert("Error");
    })
  }

  detail(id){
    this.navCtrl.push(BeritaPage,{
      id:id
    })
  }

}



