import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {AuthProvider} from "../../providers/auth/auth";
import {HomePage} from "../home/home";
import {ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  username:string = ''
  password:string = ''
  constructor(public navCtrl: NavController, public navParams: NavParams,private auth:AuthProvider,private  api:ApiProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }
  login(){
    console.log(this.password);
    console.log(this.username);
    // this.api._startSpin();
    // this.api._removeSpin();
    this.api.login(this.username,this.password).subscribe(data => {
      console.log(data)
      this.api.token = data.data.token;
      console.log("Token Registered "+this.api.token);
      this.navCtrl.push(HomePage);
      this.auth._setLogin(true);
    },error => {
      console.log(error)
      this.api._alert("Username & Password Salah");
    });
  }
  ionViewWillEnter(){
    console.log("Enter")
    this.auth._setLogin(false);
  }

}



